// 
import Vue from 'vue'
Vue.filter('transformTab',function(value){
  if(!value) return ''
  //  
  const isEnglish = value.match(/[a-zA-Z]/)
  if(isEnglish){
    switch (value) {
      case 'all':return'';
      case 'good':return'';
      case 'share':return'';
      case 'ask':return'';
      case 'job':return'';
      case 'dev':return'';
    }
  }else{
    switch (value) {
      case '':return'all';
      case '':return'good';
      case '':return'share';
      case '':return'ask';
      case '':return'job';
      case '':return'dev';
    }
  }
})