<template>
  <div class="create-post">
    <Panel>
      <template #header>
        <router-link to="/"></router-link>
        <span class="gang">/</span>
        <span v-if="$route.path === '/post/create'"></span>
        <span v-else class="edit-topic"></span>
      </template>
      <template #content>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          class="demo-ruleForm"
        >
          <el-form-item prop="tab">
            <span></span>
            <el-select v-model="ruleForm.tab" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="title">
            <el-input
              v-model="ruleForm.title"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item prop="content">
            <mavon-editor
              :toolbars="markdownOption"
              v-model="ruleForm.content"
            />
          </el-form-item>
          <el-button type="primary" @click="submit"></el-button>
        </el-form>
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Panel from "../containers/Panel.vue";
import axios from "../plugins/axios";
import Turndown from "turndown";
export default {
  components: { Panel },
  data() {
    return {
      options: [
        {
          value: "ask",
          label: "",
        },
        {
          value: "share",
          label: "",
        },
        {
          value: "job",
          label: "",
        },
        {
          value: "dev",
          label: "",
        },
      ],
      markdownOption: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        quote: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true, // 
      },
      ruleForm: {
        title: "",
        tab: "",
        content: "",
      },
      rules: {
        tab: [{ required: true, message: "", trigger: "change" }],
        title: [
          { required: true, message: "", trigger: "blur" },
          { min: 10, message: "10", trigger: "blur" },
        ],
        content: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  computed: {
    ...mapState({
      post: (state) => state.showCurrentPost.post,
    }),
    id() {
      return this.$route.params.id;
    },
  },
  async created() {
    if (this.id) {
      await this.getPost(this.id);
      console.log(this.post);
      var turndownService = new Turndown();
      const { title, tab, content } = this.post;
      const newContent = turndownService.turndown(
        content.replace('<div class="markdown-text">', "").replace("</div>", "")
      );
      this.ruleForm = { title, tab, content: newContent };
    }
  },
  methods: {
    ...mapActions(["getPost"]),
    submit() {
      // 
      this.$refs.ruleForm.validate(async (boolean) => {
        if (boolean) {
            let { title, tab, content } = this.ruleForm;
            const accesstoken = localStorage.getItem("token");
            const res = await axios.post("/topics", {
              tab,
              title,
              content,
              accesstoken,
            });
            // console.log(this.$router);
            await this.$router.push(`/post/${res.topic_id}`);
        }
      });
    },
  },
};
</script>

<style>
.gang {
  margin: 0 10px;
}
.edit-topic{
  color: rgb(112, 112, 112);
}
</style>